html {
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  height: 40px;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  height: 408px;
  padding: 0 17px;
  transition: all, all .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.card__name {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
}

.card__buy {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  scale: 1.2;
}

.card:hover .card__name {
  color: #34568b;
}

.card:hover .card__buy {
  color: #00acdc;
  background-color: #fff;
}

.reviews {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-top: 17px;
  display: flex;
}

.reviews__counter {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
}

.price_tag {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-top: 23px;
  display: flex;
}

.price_tag__text {
  color: #616070;
  font-size: 12px;
}

.price_tag__number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width <= 487px) {
  .container {
    grid-template-columns: repeat(1, 200px);
  }
}

@media screen and (width >= 488px) and (width <= 767px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) and (width <= 1023px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.navigation {
  align-items: center;
  display: flex;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation__item {
  margin: 0 10px;
}

.navigation__item:first-child {
  margin-left: 0;
}

.navigation__item:last-child {
  margin-right: 0;
}

.navigation__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.navigation__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
}
